import api from "../../api/apiKeywords";

let initialState = {
    data:null
}

const ACTION_GET_KEYWORDS = "keywords/get";

export default function  KeywordsReducer (state = initialState, action){
    switch (action.type) {
        case ACTION_GET_KEYWORDS: {
            return {
                data: action.data
            };
        };
        default: return state;
    }
}
const ActionKeywords = (data) => ({
    type: ACTION_GET_KEYWORDS,
    data: data
});

export function ThunkCretorGetKeywords() {
    return (dispatch) => {
        api.get().then((response) => {
            if (response.status == 200) {
                let result = response.data;
                dispatch(ActionKeywords(result.value));
            }
        });
    }
}