import api from "../../api/apiAbout";

let initialState = {
    aboutText:null
}

const ACTION_GET_ABOUT_TEXT = "page/about/get/text";

export default function AboutReducer(state = initialState, action) {
    switch (action.type) {
        case ACTION_GET_ABOUT_TEXT: {
            return {
                aboutText: action.data
            };
        };
        default: return state;
    }
}
const ActionAboutText = (data) => ({
    type: ACTION_GET_ABOUT_TEXT,
    data: data
});

export function ThunkCretorGetAboutText() {
    return (dispatch) => {
        api.get().then((response) => {
            if (response.status == 200) {
                let result = response.data;
                dispatch(ActionAboutText(result.value));
            }
        });
    }
}