import React from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import withRouter from "../../withRouter/withRouter";
import Articles from "./articlesPage";
import {ThunkCretorGetArticles} from "../../../context/reducers/articlesReducer";
function ArticlesPageContainer(props)
{
    if(props.articles.data == null)
        props.getArticles();

    if (props.articles.data)
        return <Articles {...props.articles} />;
}

let mapStateToProps = (state) => {
    return {
        articles : state.articles
    }
};


const mapDispatchToProps = (dispatch)=>({
    getArticles: () => {
      dispatch(ThunkCretorGetArticles());
    }
});
export default compose(
    connect(mapStateToProps, mapDispatchToProps)
)(ArticlesPageContainer);