import api from "../../api/apiExperience";

let initialState = {
    data:null
}

const ACTION_GET_EXPERIENCE = "page/experience/get";

export default function ExperienceReducer(state = initialState, action) {
    switch (action.type) {
        case ACTION_GET_EXPERIENCE: {
            return {
                data: action.data
            };
        };
        default: return state;
    }
}
const ActionExperience = (data) => ({
    type: ACTION_GET_EXPERIENCE,
    data: data
});

export function ThunkCretorGetExperience() {
    return (dispatch) => {
        api.get().then((response) => {
            if (response.status == 200) {
                let result = response.data;
                dispatch(ActionExperience(result.value));
            }
        });
    }
}