import api from "../../api/apiInfo";

let initialState = {
    data:null
}

const ACTION_GET_INFO = "page/info/get/";

export default function InfoReducer(state = initialState, action) {
    switch (action.type) {
        case ACTION_GET_INFO: {
            return {
                data: action.data
            };
        };
        default: return state;
    }
}
const ActionInfo = (data) => ({
    type: ACTION_GET_INFO,
    data: data
});

export function ThunkCretorGetInfo() {
    return (dispatch) => {
        api.get().then((response) => {
            if (response.status == 200) {
                let result = response.data;
                dispatch(ActionInfo(result.value));
            }
        });
    }
}